import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

function getPhoto(userWord) {
  const BASE_URL = 'https://pixabay.com';
  const END_POINT = '/api/';
  const params = new URLSearchParams({
    key: '44400646-52d28046ae8ecec8adffea605',
    q: userWord,
    image_type: 'photo',
    orientation: 'horizontal',
    safesearch: 'true',
  });
  const url = `${BASE_URL}${END_POINT}?${params}`;
  return fetch(url).then(res => res.json());
}

function imgTemplate(photo) {
  return photo.hits.map(
    hit => `<li class="gallery-item">
    <a href=${hit.largeImageURL}>
     <img
      class="gallery-image"
      src= ${hit.webformatURL}
      alt=${hit.tags}
     />
    </a>
    <div class="image-container">
      <ul class="text-list">
        <li>
          <h3>Likes</h3>
          <p class="image-description">${hit.likes}</p>
        </li>
        <li>
          <h3>Views</h3>
          <p class="image-description">${hit.views}</p>
        </li>
        <li>
          <h3>Comments</h3>
          <p class="image-description">${hit.comments}</p>
        </li>
        <li>
          <h3>Downloads</h3>
          <p class="image-description">${hit.downloads}</p>
        </li>
      </ul>
    </div>
  </li>`
  );
}

const formEl = document.querySelector('.form-search');
const imagesUl = document.querySelector('.gallery');

formEl.addEventListener('submit', e => {
  e.preventDefault();
  const value = formEl.elements.request.value;
  getPhoto(value)
    .then(data => {
      if (!data.hits.length) {
        iziToast.show({
          message:
            'Sorry, there are no images matching your search query. Please try again!',
          messageColor: 'white',
          backgroundColor: 'red',
          progressBarColor: 'white',
        });
      }
      const markup = imgTemplate(data);
      imagesUl.innerHTML = markup;
      console.log(data);
    })
    .catch(err => {
      console.log(err);
    });
  formEl.reset();
});

var lightbox = new SimpleLightbox('.gallery a', {});
